body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App-header {
  background: -webkit-linear-gradient(left,#fce4ed,#ffe8cc);
  background: linear-gradient(to right,#fce4ed,#ffe8cc);
  min-height: 45px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  color: #333;
  padding: 10px;
}

.App-link {
  color: #333;
  padding: 13px 10px;
  background: rgb(238, 170, 87, 0.8);
  text-decoration: none;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.App-link:hover {
  background: rgb(238, 170, 87, 1);
}
.search-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.search-wrapper form {
  margin: 20px;
}

.search {
  width: 200px;
  height: 31px;
  padding: 0 10px;
  margin: 5px;
  font-size: 14px;
  border: 1px solid #A4A4A4;
  border-radius: 4px;
}

select {
  height: 33px;
  margin: 5px;
  vertical-align: bottom;
}

.btn {
  padding: 7px 20px;
  border-radius: 4px;
  border: 1px solid #A4A4A4;
  font-size: 14px;
  margin: 5px;
}
.card-container .card {
  background-color: #fff;
  box-shadow: 0 8px 6px -6px #9c9875;
  padding: 5px;
  border-radius: 3px;
  border: 1px solid #A4A4A4;
  min-height: 350px;
}

.card img {
  max-width: 100%;
  height: 130px;
  margin: 0 auto;
  display: block;
}

.card .content {
  margin: 0 0 20px;
  min-height: 60px;
  overflow: hidden;
}

.card .tags {
  overflow: hidden;
  max-height: 50px;
}

.card .tags .tag {
  background: -webkit-linear-gradient(left,#fce4ed,#ffe8cc);
  background: linear-gradient(to right,#fce4ed,#ffe8cc);
  margin: 3px 5px;
  display: inline-block;
  border-radius: 2px;
  padding: 2px 8px 4px;
  color: #333;
  font-size: 9px;
}
.card-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(190px, 1fr));
  grid-auto-flow: row;
  grid-gap: 10px;
  max-width: 1240px;
  padding: 0 20px;
  margin: 0 auto 20px;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.card-count {
  text-align: center;
  margin: 0 0 20px;
}
body {
  /* background-color: #edebde; */
  background-color: #fff;
}

.App {
  text-align: left;
}

h1 {
  font-size: 16px;
  margin: 10px 0 0;
}

h3 {
  font-size: 14px;
  margin: 10px 0;
  overflow: hidden;
}

h3 span {
  font-size: 12px;
}

h4 {
  margin: 0 0 5px;
}

p {
  margin: 0;
  padding: 0;
  font-size: 12px;
}

a {
  color: #eeaa57;
  text-decoration: none;
}

.wrapper {
  padding: 40px;
}
